class MessageParser {
    constructor(actionProvider, state) {
      this.actionProvider = actionProvider;
      this.state = state;
    }
  
    parse(message) {
      console.log(message)
      const lowercase = message.toLowerCase();
    
      if (lowercase.includes("hello")){
        this.actionProvider.greet();
      }
      if (lowercase.includes("cars")){
        this.actionProvider.handleCars();
      }
      if (lowercase.includes("boat")){
        this.actionProvider.handleBoat();
      }
      if (lowercase.includes("apartments")){
        this.actionProvider.handleApartments();
      }
      else{
          this.actionProvider.handleNoInput();
      }
    }
  }
  
  export default MessageParser;